}

/* Îã§ÌÅ¨Î™®ÎìúÏóêÏÑú Ï≤¥ÌÅ¨Î∞ïÏä§ÏôÄ ÎùºÎîîÏò§ Í∞úÏÑ† */
.dark input[type="checkbox"],
